@-webkit-keyframes up-down {
  0% {
    -webkit-transform: translateY(15%);
            transform: translateY(15%);
  }
  50% {
    -webkit-transform: translateY(-5%);
            transform: translateY(-5%);
  }
  100% {
    -webkit-transform: translateY(15%);
            transform: translateY(15%);
  }
}

@keyframes up-down {
  0% {
    -webkit-transform: translateY(15%);
            transform: translateY(15%);
  }
  50% {
    -webkit-transform: translateY(-5%);
            transform: translateY(-5%);
  }
  100% {
    -webkit-transform: translateY(15%);
            transform: translateY(15%);
  }
}

